<template>
    <div>
        <input @change="change" type="file" ref="file">
        <button @click="importJSON">ImportJSON</button>
        <div>
            {{ users.length }}
            <ul v-for="user in users" :key="user.index">
                <li>{{ user.fullnameEng }}</li>
            </ul>
        </div>
    </div>
</template>

<script>
    export default {
        data: () => ({
            users: [],
        }),
        methods: {
            change() {
                let vm = this;
                let file = this.$refs['file'].files[0];
                let reader = new FileReader();
                reader.onload = onReaderLoad;
                reader.readAsText(file);
                function onReaderLoad(event){
                    vm.users = JSON.parse(event.target.result);
                }
            },
            importJSON () {
                this.$axios.post('auth/import-users-json', this.users)
            }

        }
    }
</script>
